.phone {
	position: relative;
	width: 350px;
	height: 500px;
	padding: 30px;
	padding-top: 50px;
	border: 10px solid #f4f4f4;
	border-radius: 30px;
	overflow: hidden;
}

.tinderCards__empty {
	transform: rotate(-20deg);
	font-size: 3rem;
	text-transform: uppercase;
	border: 10px solid red;
	padding: 8px;
	margin-top: 120px;
}

.tinderCards__container {
	display: flex;
	justify-content: center;
}

.right-exit {
	transform: scale(0.8) rotateZ(0deg);
}

.right-exit-active {
	transform: scale(1.1) translate(500px, -150px) rotateZ(20deg);
	transition: all 500ms ease-out;
}

.left-exit {
	transform: scale(0.8) rotateZ(0deg);
}

.left-exit-active {
	transform: scale(1.1) translate(-500px, -150px) rotateZ(-20deg);
	transition: all 500ms ease-out;
}

.tinderCards__swipe {
	position: absolute;
	z-index: 6;
}

.card__container {
	position: relative;
	width: 250px;
	height: 300px;
}

.card__triangle {
	position: absolute;
	bottom: 10px;
	right: 20px;
	z-index: 1;
	border-width: 10px 0 10px 20px;
	border-color: transparent transparent transparent #f4f4f4;
	border-style: solid;
}

.toggler {
	position: absolute;
	bottom: 10px;
	right: 20px;
	z-index: 2;
	cursor: pointer;
	width: 30px;
	height: 30px;
	opacity: 0;
}

.toggler:checked + .card {
	transform: rotateY(180deg);
}

.card {
	position: absolute;
	width: 100%;
	height: 100%;
	transform-style: preserve-3d;
	transition: all 1.5s ease;
}

.card__front {
	position: absolute;
	width: 100%;
	height: 100%;
	border-radius: 15px;
	background-size: cover;
	background-position: center;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

.card__name-age {
	position: absolute;
	bottom: 10px;
	left: 30px;
	color: #fff;
	opacity: 0.9;
	font-size: 1.5rem;
}

.card__back {
	position: absolute;
	width: 100%;
	height: 100%;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	background: #fff;
	transform: rotateY(180deg);
	border-radius: 10px;
	box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
}

.card__back > div {
	margin-top: 20px;
	margin-left: 20px;
}

.card__back > div > div {
	font-size: 20px;
	margin-top: 20px;
}

.card__back > div > dl {
	font-size: 20px;
}

.button {
	position: absolute;
	bottom: 30px;
	left: 0;
	display: flex;
	justify-content: space-around;
	width: 100%;
}

.button .MuiIconButton-root {
	background-color: white;
	box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.18) !important;
}

.button__dislike {
	color: #fe3c72 !important;
}

.button__like {
	color: #63dc99 !important;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

body {
	padding: 0;
	margin: 0;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

